import React from 'react';
import { Link } from '../../../node_modules/react-router-dom/index';
import Button from '../../common/Button';
import '../../styles/AuthForm.scss';

/**     . */

const textMap = {
    login: '',
    register: '',
}


const AuthForm = ({type, form, onChange, onSubmit}) => {
    const text = textMap[type];

    return (
        <div className='AuthForm'>
            <h3>{text}</h3>
            <form onSubmit={onSubmit}>
                <input className="StyledInput" onChange={onChange} value={form.email} autoComplete='usename' name='email' type="text" placeholder=''/>
                {type === 'register' && (
                    <>
                        {/* <Button className="StyledInput"> </Button>
                        <input className="StyledInput" name='name' type="text" placeholder=' '/>
                        <Button className="StyledInput"> </Button> */}
                    </>
                )}
                <input className="StyledInput" onChange={onChange} value={form.password} autoComplete='new-password' name='password' type="password" placeholder=''/>
                {type === 'register' && (
                    <>
                        <input className="StyledInput" onChange={onChange} value={form.name} name='name' type="text" placeholder=''/>
                        <input className="StyledInput" onChange={onChange} value={form.birth} name='birth' type="text" placeholder=''/>
                        <input className="StyledInput" onChange={onChange} value={form.gender} name='gender' type="text" placeholder=''/>
                        <input className="StyledInput" onChange={onChange} value={form.phone} name='phone' type="text" placeholder=' '/>
                    </>
                )}
            <Button cyan={true} fullWidth={true} marginTop={true}>
                    {text}
            </Button>
                
            </form>
            
            
            <div className="Footer">
                {type === 'login' ? (
                    <Link to='/register'></Link>
                ) : (
                    <Link to='/login'></Link>
                )}
               
            </div>
            
            
        </div>
    );
};

export default AuthForm;